import style from '../styles/about';
import { h, Component } from 'preact';

export default class About extends Component {

    state = {
        active: null
    }

    render() {
        
        const flexcontainer = {
            display: "flex",
            flexDirection: "row",
            flex: "1 1 auto",
            justifyContent: "center",
            minWidth: 504,
            overflow: "hidden"
        }

        const containerstyle = {
            flex: "0 0 auto",
            width: 560,
            boxShadow: "-6px 5px 10px 3px rgba(0,0,0,0.09)",
            padding: "20px",
            margin: "40px 0px 40px 0px",
        }

        const textstyle = {
            flex: "0 0 auto",
            width: 310,
            padding: "0px 0px 8px 0px",
            margin: "46px 0px 0px 0px",
        }

        const textcontainer = {
            fontSize: "0.8em",
            background: "#3A7BD4",
            padding: 10,
            color: "white",
        }
        return <div>
               Buttonkit is a "Software as a Service" that I have been building over the last few years.  It can be thought of as a remote control for buildings.  Click on the video below to get an overview of what it is and what it provides.
               <div style={flexcontainer}>
                    <div  style={containerstyle}>
                            <video  controls class={style.vid} onclick={(e)=>{
                            if (e.target.paused) {
                                e.target.play();
                            } else {
                                e.target.pause();
                            }
                            }}>
                                <source  src={"../../assets/buttonkit/buttonkit.mp4"} />
                            </video>
                    </div>
                    
                    <div style={textstyle}>
                    <div style={textcontainer}>
                     The Buttonkit app/webapp presents a simple panel of buttons to users. When pressed, a button efficiently gathers the information it needs to perform a task.  Tasks may be anything from parcel collection and pickup, to reactive repairs, to boiler fault diagnosis to noise complaints and resource bookings.  The novelty of the approach comes from the ease with which new buttons can be created, published, unpublished and provided to any stakeholders involved in a particular development (i.e. staff and contractors as well as residents).  
                     </div>
                    </div>
                </div>
                An early release of Buttonkit is being trialled in a large residential development in London's Docklands to manage all of their parcel deliveries.  The buttons mirror the processes that the concierge team already had in place; with a single tablet, all staff can quickly and efficiently log, track and notify residents of new deliveries.
            </div>
                
    }
}





