import { h, Component } from 'preact';
import style from './styles/bootstrap'; 
import BootstrapUI from './bootstrapui';
import Master from './master';

const descriptions = {
    bootstrap: [
        "This is a nodejs", 
        "server that creates new",
        "sites.  It communicates", 
        "with the kubernetes API", 
        "to create a new service,",
        "ingress route and pod."
    ],
}

const descriptionplacement = {
    bootstrap:{x:336,y:149.5},
}

const descriptionstyle = {
    fontFamily:`'GillSans', 'Gill Sans', sans-serif`,
    fontSize: 3.5,
    fill: "white",
    textAnchor: "middle",
}


const labels = {
    bootstrap: {x:"334.5px", y:"142px"},
}


const circles = {
    bootstrap:{cx:334.5,cy:136.5},
}

const paths = {
    bootstrap : [
        "M339.763,132.327l-5.165,3.021l0.092,4.686l5.1,-2.949l-0.027,-4.758Z",
        "M328.593,132.656l5.209,2.828l0.081,4.46l-5.121,-3.084l-0.169,-4.204Z",
        "M334.034,129.026l-5.067,2.941l5.108,2.836l5.332,-3.195l-5.373,-2.582Z",
    ],
}

export default class Bootstrap extends Component {
    
    state = {
        active: null,
        container: "bootstrap",
    }

    makeActive(item){
        this.props.onActive(item);
    }

    stopActive(item){
        this.props.onActive(null);
    }

    selectContainer(c){
        this.setState({container:c});
    }

    renderPath(item){
        const textstyle =  {
            fontFamily:`'GillSans', 'Gill Sans', sans-serif`,
            fontSize: this.props.active ? 3: 3.476,
            fill: this.props.active ? "white" : "#66676a", 
            textAnchor: "middle",
        }

        const _paths = paths[item].map((p,i)=>{
            const style = {
                fill: i == 2 ? "#E25B26": "white",
                stroke:"#3a7bd4",
                strokeWidth:"0.6px"
            }
            return <path key={i} d={p} style={style}/>
        }) 

        const circleactive = {
            stroke: this.props.active && this.state.container === item ? "white" : "transparent",
            strokeWidth: this.props.active && this.state.container === item ? 0.5 : 0,
        }
        const containercircle =  this.props.active ? style.containercircleactive : style.containercircle;
    
        const overview = descriptions[item].map((t,i)=>{
            return  <text class={style.overview} style={descriptionstyle} x={`${descriptionplacement[item].x}px`} y={`${(i*5) + descriptionplacement[item].y}px`}>{t}</text>
        })

        return <g onClick={()=>{this.selectContainer(item)}}>
            <circle cx={circles[item].cx} cy={circles[item].cy} r="9" class={containercircle} style={circleactive}/>
            {_paths}
            <text x={labels[item].x} y={labels[item].y} style={textstyle}>{item}</text>
            {this.props.active && this.state.container===item && overview}
        </g>
    }

    
    renderContainer(name){
        const container = this.props.active  ? style[`${name}container`] : {};
        
        return <g onClick={()=>this.selectContainer(name)} class={container}>
                   {this.renderPath(name)}
                </g>
    }


    render(){
     
        const cname = this.props.active  ? style.pod : {}; 
        const crect = this.props.active  ? style.activerect : style.hiddenrect; 
        const circleclass = this.props.active ? style.activepodcircle : style.podcircle; 
        if (!this.props.active && this.props.expanded){
            return null;
        }
        return  <g>
                    {this.props.active &&  <rect onClick={()=>this.stopActive()} x="0" y="0" width="819" height="465" class={crect} style="fill:#e5f0ff;fill-opacity:0.85098;stroke:#fff;stroke-width:2.5px;"/>}
                    <g class={cname} onClick={()=>this.makeActive("bootstrap")}>
                        <circle class={circleclass} cx="334.708" cy="137.186" r="21.415" style="fill:#e8e8e8;stroke:#3a7bd4;stroke-width:1px;"/>
                        <text x="329.136px" y="113px" style="font-family:'GillSans', 'Gill Sans', sans-serif;font-size:5.581px;fill:#3a7bd4;">pod</text>
                        
                        {this.renderContainer("bootstrap")}
                       
                        <g transform="matrix(0.310061,0,0,0.310061,-2296.78,141.237)">
                            <text x="8420.21px" y="69.363px" style="font-family:'GillSans', 'Gill Sans', sans-serif;font-size:14.013px;fill:#acaeb1;">bootstrap.buttonkit.com</text>
                        </g>
                    
                    </g>                
                    <BootstrapUI  active={this.props.active} onActive={()=>this.makeActive("bootstrap")}/>
                    <Master active={this.props.active} route="bootstrap"/>
                </g>
    }
}


