import { h, Component } from 'preact';
import style from './styles/worker'; 
import WorkerUI from './workerui';
import Master from './master';

const descriptions = {
    panel: [
        "panel is a nodejs server", 
        "that serves up the",
        "Buttonkit webapp and ",
        "exposes the Buttonkit API", 
        "for apps/webapps. It handles",
        "button submissions, new ",
        "button installs and ",
        "performs standard auth / ",
        "user management"
    ],


    mongo: [
        "mongodb is used",
        "for storing data when",
        "a button is configured",
        "to do so. Buttons record",
        "schemas that are used",
        "to control how the data is",
        "displayed back",
        "to users."
    ],

    redis:  [ 
        "redis is a fast,", 
        "lightweight key storage",
        "database, used to",
        "persist session data."
    ],

    postgres:  [ 
        "postgres is used to ", 
        "store the core Buttonkit",
        "data (buttons, users, groups",
        "building structure).  It ",
        "replaces neo4j which, though",
        "good for modelling building",
        "structure, was found to be ",
        "too resource intensive."
    ],

    rabbitmq:  [ 
        "rabbitmq is a ",
        "queue between panel" ,
        "and worker.  Panel ",
        "enqueues long running tasks",
        "(e.g. external requests) which",
        "are dequeued and",
        "processed by",
        "worker."
    ],

    worker:  [ 
        "worker is a",
        "nodejs server that",
        "listens for requests on",
        "the rabbitmq queue and ",
        "processes them (typically",
        "a GET or POST to an",
        "endpoint."
    ]
}

