import style from '../styles/design';
// <img src="../../assets/databox/mockupsalad.png" />
import { h, Component } from 'preact';
import  config from '../../../config'; 
const {ROOTDIR} = config;

const images = [
    {
        id: "buttonkitvid",
        src: `${ROOTDIR}/buttonkit/buttonkitlogo.png`,
        video: "../../assets/buttonkit/buttonkit.mp4",
        description: "video: buttonkit overview",
        overview: "A keynote-created video used to explain buttonkit.  Have a quick look if you have the time, it gets the idea across and showcases a bit of my design"
    },
    {
        id: "website",
        src: `${ROOTDIR}/buttonkit/homepage.png`,
        description: "image: website homepage",
        overview: "This is the main splash screen for the buttonkit site.  I wanted to keep it simple/abstract and postery"
  
    }, 
    {
        id: "mobile",
        src: `${ROOTDIR}/buttonkit/mobile.png`,
        description: "image: mobile login",
        overview: "This is the main login page to the iOS Buttonkit app"
  
    },
    {
        id: "kit1",
        src: `${ROOTDIR}/buttonkit/kit1.png`,
        description: "image: promotional graphics",
        overview: "Promotional graphics for Buttonkit (inspired by airfix kits)"
  
    }, 
    {
        id: "kit2",
        src: `${ROOTDIR}/buttonkit/kit2.png`,
        description: "image: promotional graphics",
        overview: "Promotional graphics for Buttonkit (inspired by airfix kits)"
  
    },
    {
        id: "kit3",
        src: `${ROOTDIR}/buttonkit/kit3.png`,
        description: "image: promotional graphics",
        overview: "Promotional graphics for Buttonkit (inspired by airfix kits) showing a variety of services that it can perform"
  
    }  
];

export default class Design extends Component {

    state = {
        active: null
    }

    selectItem = (item) => {
       
        const {id} = item || {};
        if (id !== this.state.active) {
            const left = this[`image_${id}`] ?  Math.max(35, (this.gallery.getBoundingClientRect().width - 750 - (200/2)) /2) : 0;
            const top  = this[`image_${id}`]  ? Math.max(120, (this.gallery.getBoundingClientRect().height - 400) /2): 0;
            this.setState({ active: id, left, top, item});
        } 
        else {
            this.setState({ active: null, left: 0, top:0, item:null });
        }

    };

    renderTab(){
        return <div  onClick={() => { this.selectItem(null) }} class={style.tabtitle} style={{top: -40, left:0}}>
           {this.state.item.id}
        </div>
    }

    renderDescription(){

        const cstyle = {
            height: this.state.active==="buttonkitvid" ? 590: 530,
            left: 770,
            top: 0,
        }

        return <div  onClick={() => { this.selectItem(null) }} class={style.overview} style={cstyle}>
            {this.state.item.overview}
        
        </div>
    }

    renderActive(){
        return <div class={style.fullcontainer} style={{height: 530, width: 970, left:this.state.left+10, top: this.state.top+10}}>
         {this.renderDescription()}
         {this.renderTab()}
        </div>
       
    }

    renderItem(item){
        const imagestyle = this.state.active == item.id ? style.imageactive : style.image;
        const thumbstyle = this.state.active == item.id ? {position:'absolute', top: this.state.top, left: this.state.left, zIndex:2} : {};
        const containerstyle = {
            padding: this.state.active ? "10px" : "10px 10px 75% 10px",
            height: this.state.active ? "auto" : 0,
        }

        if (this.state.active==="buttonkitvid"){
           return <div key={item.id} ref={r => this[item.id] = r} onClick={() => { this.selectItem(item) }} class={style.thumbnail} style={thumbstyle}>
                <div class={style.imagecontainer} style={containerstyle}>
                    <video ref={r=>this.video=r} class={imagestyle} autoplay onclick={(e)=>{
                      e.target.pause()
                    }}>
                        <source  src={item.video} />
                    </video>
                </div>
                <div class={style.info}>
                    {!this.state.active && item.description}
                </div>
            </div>
        }


        return <div key={item.id} ref={r => this[item.id] = r} onClick={() => { this.selectItem(item) }} class={style.thumbnail} style={thumbstyle}>
                <div class={style.imagecontainer} style={containerstyle}>
                    <img ref={r=> this[`image_${item.id}`] = r} class={imagestyle} src={item.src} />
                </div>
                <div class={style.info}>
                    {!this.state.active && item.description}
                </div>
            </div>
    }

    render() {
        const items = images.map(i => this.renderItem(i));

        return (<div ref={r=>this.gallery=r} class={style.gallery}>
            {this.state.active && <div  onClick={() => { this.selectItem(null) }} class={style.opaque}/>}
            {this.state.active && this.renderActive()}
            {items}
        </div>)
    }
}





