import { h, Component } from 'preact';
import style from './styles/arch'; 
import CreateUI from './createui';
import Master from './master';

const descriptions = {
    "create": [
        "This is a nodejs", 
        "server that serves up the",
        "react/redux frontend that", 
        "supports new button creation.", 
        "It interfaces with github to",
        "store buttons in a user's", 
        "account.",
    ],

    "mongo": [
        "mongodb is used",
        "to store user details.",
        "This is currently overkill",
        "but it will be used",
        "more as the button",
        "creator's functionality",
        "is expanded"
    ],

    "redis":  [ 
        "redis is a fast,", 
        "lightweight key storage",
        "database, used to",
        "persist session data."
    ]
}


const descriptionstyle = {
    fontFamily:`'GillSans', 'Gill Sans', sans-serif`,
    fontSize: 4.5,
    fill: "white",
    textAnchor: "middle",
}

export default class Create extends Component {
    
    state = {
        active: null,
        container: "create",
    }

    makeActive(item){
        this.props.onActive(item);
    }

    stopActive(item){
        this.props.onActive(null);
    }

    selectContainer(c){
        this.setState({container:c});
    }

    renderPath(item){
        switch(item){
            case "create":
                return this.renderCreatePath();
            case "mongo":
                return this.renderMongoPath();
            case "redis":
                return this.renderRedisPath();
            default:
                return null;
        }
    }

    renderMongoPath(){
       
        const textstyle =  {
            fontFamily:`'GillSans', 'Gill Sans', sans-serif`,
            fontSize: this.props.active ? 3: 3.483,
            fill: this.props.active ? "white" : "#66676a", 
            textAnchor: "middle",
        }
        
        const circleactive = {
            stroke: this.props.active && this.state.container === "mongo" ? "white" : "transparent",
            strokeWidth: this.props.active && this.state.container === "mongo" ? 0.5 : 0,
        }
        const containercircle =  this.props.active  ? style.containercircleactive : style.containercircle;
        
        const overview = descriptions["mongo"].map((t,i)=>{
            return  <text class={style.overview} style={descriptionstyle} x="353px" y={`${(i*6) + 195}px`}>{t}</text>
        })

        return <g onClick={()=>{this.selectContainer("mongo")}}>
            <circle cx="387" cy="224.5" r="9" class={containercircle} style={circleactive}/>
            <path d="M392.476,220.672l-5.165,3.021l0.093,4.686l5.099,-2.949l-0.027,-4.758Z" style="fill:#fff;stroke:#3a7bd4;stroke-width:0.6px;"/>
            <path d="M381.307,221.002l5.209,2.828l0.08,4.459l-5.12,-3.084l-0.169,-4.203Z" style="fill:#fff;stroke:#3a7bd4;stroke-width:0.6px;"/>
            <path d="M386.747,217.371l-5.066,2.942l5.108,2.835l5.332,-3.194l-5.374,-2.583Z" style="fill:#e25b26;stroke:#3a7bd4;stroke-width:0.6px;"/>
            <text x="387px" y="230.439px" style={textstyle}>mongodb</text>
            {this.props.active && this.state.container==="mongo" && overview}
        </g>
    }

    renderCreatePath(){
        
        const textstyle =  {
            fontFamily:`'GillSans', 'Gill Sans', sans-serif`,
            fontSize: this.props.active ? 3: 3.483,
            fill: this.props.active ? "white" : "#66676a", 
            textAnchor: "middle",
        }
        
        const containercircle =  this.props.active  ? style.containercircleactive : style.containercircle;
        
        const circleactive = {
            stroke: this.props.active && this.state.container === "create" ? "white" : "transparent",
            strokeWidth: this.props.active && this.state.container === "create" ? 0.5 : 0,
        }

        const overview = descriptions["create"].map((t,i)=>{
            return <text class={style.overview} style={descriptionstyle} x="371px" y={`${(i*6) + 236}px`}>{t}</text>
        })
       
        return <g onClick={()=>{this.selectContainer("create")}}>
            <circle cx="371.5" cy="219.279" r="9" class={containercircle} style={circleactive}/>
            <path d="M376.983,214.884l-5.165,3.021l0.092,4.686l5.1,-2.95l-0.027,-4.757Z" style="fill:#fff;stroke:#3a7bd4;stroke-width:0.6px;"/>
            <path d="M365.813,215.213l5.209,2.828l0.08,4.459l-5.12,-3.084l-0.169,-4.203Z" style="fill:#fff;stroke:#3a7bd4;stroke-width:0.6px;"/>
            <path d="M371.254,211.583l-5.067,2.941l5.108,2.835l5.332,-3.194l-5.373,-2.582Z" style="fill:#e25b26;stroke:#3a7bd4;stroke-width:0.6px;"/>
            <text x="371.5px" y="225.455px" style={textstyle}>create</text>
            {this.props.active && this.state.container==="create" && overview}
        </g>
    }
    
    renderRedisPath(){
        const textstyle =  {
            fontFamily:`'GillSans', 'Gill Sans', sans-serif`,
            fontSize: this.props.active ? 3: 3.483,
            fill: this.props.active ? "white" : "#66676a", 
            textAnchor: "middle",
        }
        
        const containercircle =  this.props.active ? style.containercircleactive : style.containercircle;

        const circleactive = {
            stroke: this.props.active && this.state.container === "redis" ? "white" : "transparent",
            strokeWidth: this.props.active && this.state.container === "redis" ? 0.5 : 0,
        }

        const overview = descriptions["redis"].map((t,i)=>{
            return <text class={style.overview} style={descriptionstyle} x="419px" y={`${(i*6) + 182}px`}>{t}</text>
        });

        return <g onClick={()=>{this.selectContainer("redis")}}>
            <circle cx="384" cy="205" r="9" class={containercircle} style={circleactive}/>
            <path d="M389.426,202.03l-5.165,3.021l0.093,4.686l5.099,-2.95l-0.027,-4.757Z" style="fill:#fff;stroke:#3a7bd4;stroke-width:0.6px;"/>
            <path d="M378.257,202.359l5.209,2.828l0.08,4.459l-5.12,-3.084l-0.169,-4.203Z" style="fill:#fff;stroke:#3a7bd4;stroke-width:0.6px;"/>
            <path d="M383.697,198.729l-5.066,2.941l5.108,2.835l5.332,-3.194l-5.374,-2.582Z" style="fill:#e25b26;stroke:#3a7bd4;stroke-width:0.6px;"/>
            <text x="384px" y="212px" style={textstyle}>redis</text>
            {this.props.active && this.state.container==="redis" && overview}
        </g>
    }

    renderContainer(name){
        const container = this.props.active ? style[`${name}container`] : {};
        
        return <g onClick={()=>this.selectContainer(name)} class={container}>
                   {this.renderPath(name)}
                </g>
    }


    render(){
        console.log("in create and this props ative is", this.props.active);

        const cname = this.props.active ? style.pod : {}; 
        const crect = this.props.active ? style.activerect : style.hiddenrect; 
        const circleclass = this.props.active ? style.activepodcircle : style.podcircle; 

        return  <g>
                    <rect onClick={()=>this.stopActive()} x="0" y="0" width="819" height="465" class={crect} style="fill:#e5f0ff;fill-opacity:0.85098;stroke:#fff;stroke-width:2.5px;"/>
                    <g onClick={()=>this.makeActive("createpod")} class={cname}>
                        <circle cx="381.102" cy="217.279" r="21.415" class={circleclass}/>
                        <text x="375.956px" y="191.715px" style="font-family:'GillSans', 'Gill Sans', sans-serif;font-size:5.581px;fill:#3a7bd4;">pod</text>
                        
                        {this.renderContainer("create")}
                        {this.renderContainer("mongo")}
                        {this.renderContainer("redis")}
                        
                        <g transform="matrix(0.310061,0,0,0.310061,-2246.21,221.909)">
                            <text x="8420.21px" y="70.363px" style="font-family:'GillSans', 'Gill Sans', sans-serif;font-size:14.013px;fill:#93969a;">create.buttonkit.com</text>
                        </g>
                       
                    </g>
                    <g onClick={()=>this.makeActive("createpod")}>
                        <CreateUI active={this.props.active}/>
                    </g>
                    <Master active={this.props.active} route="create"/>
                </g>
    }
}


