import { h, Component } from 'preact';
import style from './styles/bootstrapui'; 

const overviewtext = [
    `The bootstrap site collects the info`,
    `it needs to build a new Buttonkit`,
    `instance.  It requests information on `,
    `the structure of buildings (blocks)`,
    `in a development and the subdomain `, 
    `for the new site.  This information `,
    `is written to a kubernetes configMap `,
    `and used by a new instance to `,
    `generate a (buttonless) site.  The `,
    `new instance has an admin section` ,
    `for creating new users and an`,
    `initial set of buttons.`,
]

export default class BootstrapUI extends Component {

    renderOverview(){
        const description = overviewtext.map((t,i)=>{
            return <text class={style.descriptiontext} x="45px" y={`${(i*14) + 190}px`}>{t}</text>
        })
       

        return <g>
                <rect class={style.overviewrect} x="36.509" y="80.746" rx="2" ry="2" width="200.033" height="280" style="fill:#3a7bd4;stroke:#3a7bd4;stroke-width:1px;"/>
                <g class={style.description}>{description}</g>
            </g>
    }

    render(){
       
        const textclass = this.props.active? style.bootstraptextactive : style.boostraptext;
        const cname = this.props.active ? style.boostrapuiactive : style.boostrapui;
       
        const labelstyle = {
            fontFamily:`'GillSans', 'Gill Sans', sans-serif`,
            fontSize:`11.173px`,
            fill:`#66676a`,
            opacity: this.props.active ? 0 : 1.0,
        }
        return (
            <g>
                {this.props.active && this.renderOverview()}
                <g class={cname} onClick={()=>this.props.onActive()}>
                    <path d="M78.697,139.575c0,-2.37 -1.924,-4.294 -4.294,-4.294l-69.609,0c-2.37,0 -4.294,1.924 -4.294,4.294l0,39.123c0,2.37 1.924,4.294 4.294,4.294l69.609,0c2.37,0 4.294,-1.924 4.294,-4.294l0,-39.123Z" style="fill:#fefefe;stroke:#3a7bd4;stroke-width:1px;"/>
                    <path d="M24.829,143.228c0.782,0 1.417,0.649 1.417,1.448l0,30.474l-19.068,0l0,-30.474c0,-0.799 0.635,-1.448 1.417,-1.448l16.234,0Z" style="fill:#fff;stroke:#3a7bd4;stroke-width:1px;"/>
                    <path d="M29.276,188.504c0.423,-4.378 3.861,-7.796 8.033,-7.796c4.171,0 7.609,3.418 8.032,7.796l-16.065,0Z" style="fill:#fff;stroke:#3a7bd4;stroke-width:1px;"/>
                    <rect x="20.206" y="146.464" width="4.035" height="4.169" style="fill:#fff;stroke:#3a7bd4;stroke-width:1px;"/>
                    <rect x="8.947" y="146.471" width="4.035" height="4.169" style="fill:#fff;stroke:#3a7bd4;stroke-width:1px;"/>
                    <rect x="14.344" y="146.472" width="4.035" height="4.169" style="fill:#e25b26;stroke:#3a7bd4;stroke-width:1px;"/>
                    <rect x="20.07" y="152.185" width="4.035" height="4.169" style="fill:#e25b26;stroke:#3a7bd4;stroke-width:1px;"/>
                    <rect x="8.811" y="152.192" width="4.035" height="4.169" style="fill:#fff;stroke:#3a7bd4;stroke-width:1px;"/>
                    <rect x="14.207" y="152.193" width="4.035" height="4.169" style="fill:#fff;stroke:#3a7bd4;stroke-width:1px;"/>
                    <rect x="20.137" y="157.842" width="4.035" height="4.169" style="fill:#fff;stroke:#3a7bd4;stroke-width:1px;"/>
                    <rect x="8.878" y="157.849" width="4.035" height="4.169" style="fill:#fff;stroke:#3a7bd4;stroke-width:1px;"/>
                    <rect x="14.275" y="157.849" width="4.035" height="4.169" style="fill:#fff;stroke:#3a7bd4;stroke-width:1px;"/>
                    <rect x="15.248" y="168.394" width="4.035" height="6.57" style="fill:#fff;stroke:#3a7bd4;stroke-width:1px;"/>
                    <text class={textclass} x="12.397px" y="127.185px" style="font-family:'GillSans', 'Gill Sans', sans-serif;font-size:11.173px;fill:#66676a;">bootstrap ui</text>
                </g>
                
            </g>
        )
    }
}


