import style from '../styles/base';
import { h, Component } from 'preact';
import Arch from '../arch';

export default class Tech extends Component {

    state = {
        active: null
    }

    render() {
        //todo:  highlight and mention all tech!!
        return <div>
               Buttonkit components all run in docker and are managed by <span class={style.tech}>Kubernetes</span>.  One of the my goals was to keep hosting costs low (Buttonkit is a startup).  The infrastructure is capable of scaling up, but in its current form, costs just $15 a month. It is split over 3 low spec <span class={style.tech}>linux</span> servers (1GB RAM, 25GB storage) and this includes the button creation tool, the bootstrap site (which generates now buttonkit instances on demand) and the hosted buttonkit instances. The creator and bootstrap web frontends were written in <span class={style.tech}>react/redux</span> and the iOS app was written using <span class={style.tech}>react native.</span> The server components were written in <span class={style.tech}>nodejs.</span> <strong>Click on the pods in the architecture diagram</strong> (created with <span class={style.tech}>svg, css animations and preact</span>) to get a little more info.
                <Arch/>
            </div>
                
    }
}


