import localstyle from './styles/local';
import base from './styles/base';

import { Link } from 'preact-router/match';
import { h, Component } from 'preact';
import ProjectPic from './projectpic';
import FooterPic from '../../components/footer/footerpic';
import About from './routes/about';
import Tech from './routes/tech';
import Design from './routes/design';

const style = {...localstyle, ...base};

export default class Buttonkit extends Component {

    renderContent(item = "about") {

        switch (item) {
            case "tech":
                return <Tech/>;
            case "design":
                return <Design/>;

            default:
                return <About />;
        }
    }

    renderFooterImage(item = "about") {
        switch (item) {
            case "tech":
            case "about":
                return null;
            default:
                return <div class={style.footergraphic}>
                    <FooterPic />
                </div>
        }
    }

    renderLinks(item = "about") {
        return null;
        console.log(item);
        switch (item) {
            case "my":
            case "about":
                return <div class={style.linkbox}>
                    <div class={style.link}><strong>live IDE</strong>: sdk.iotdatabox.com</div>
                    <div class={style.link}><strong>github</strong>: github.com/me-box/platform-sdk</div>
                    <div class={style.link}><strong>project site</strong>: databoxproject.uk</div>
                </div>
            default:
                return null;
        }
    }

    render({ item }) {
        const containerstyle = {
            background: item==="design" ? "lightgrey" : "white"
        } 

        return (
            <div>
                <div>
                    <div class={style.summary}>
                        <div class={style.title}>buttonkit</div>
                        <div class={style.datecontainer}>
                            <div class={style.date}>
                                2014-PRESENT
                            </div>
                        </div>

                        <div class={style.projectpic}><ProjectPic /></div>

                    </div>
                    <div class={style.linkbox}>
                        <div class={style.weblabel}>project page</div>
                        <div class={style.web}>www.buttonkit.com</div>
                        <div class={style.weblabel}>button creator</div>
                        <div class={style.web}>create.buttonkit.com</div>
                        <div class={style.weblabel}>bootstrap</div>
                        <div class={style.web}>bootstrap.buttonkit.com</div>
                    </div>
                </div>
                <div class={style.contentcontainer} style={containerstyle}>
            
                    <div class={style.menu}>
                        <Link activeClassName={style.active} class={style.back} href="/sc"></Link>
                        <nav>
                            <Link activeClassName={style.active} href="/sc/project/buttonkit">about</Link>
                            <Link activeClassName={style.active} href="/sc/project/buttonkit/tech">tech</Link>
                            <Link activeClassName={style.active} href="/sc/project/buttonkit/design">design</Link>
                        </nav>
                    </div>
                    <div class={style.content}>


                        <div class={style.maincopy}>
                            {this.renderContent(item)}
                        </div>
                        {this.renderLinks(item)}
                        {this.renderFooterImage(item)}

                    </div>
                </div>
            </div>
        )
    }
}


