import { h, Component } from 'preact';
import style from './styles/master';


const rectstyle = (active)=>{
    return {
        stroke:"#3a7bd4", 
        strokeWidth:"1px", 
        fill: active ? "#E25B26" : "white"
    };
}

const textstyle = (active)=>{
    return {
        fontFamily:`GillSans', 'Gill Sans', sans-serif`,
        fontSize:"4.432px",
        fill: active ? "white" : "#acaeb1",
    }
}
export default class Master extends Component {
    
    renderRoute(route){

        const routestyle = {
            fill:"none",
            stroke:"#E25B26",
            strokeWidth:1,
           
        }

        switch (route){
            
            case "create":
                return <g>
                    <path d="M130,200l330,-5" style={routestyle}/>
                </g>
            case "bootstrap":
                return <g>
                    <path d="M130,200l120,0l42,-65l48,0l70,60l50,0" style={routestyle}/>
                </g>
            case "worker":
                return <g>
                    <path d="M130,200l120,0,l50,-33,l40,0,l65,28,l59,0" style={routestyle}/>
                    <path d="M130,200l120,0,l50,33,l40,0,l65,-38,l59,0" style={routestyle}/>
                    <path d="M130,200l120,0,l50,68,l40,0,l65,-73,l59,0" style={routestyle}/>
                
                </g>
        }
    }

    renderJoins(route){

        const circlestyle = { fill: "#3a7bd4",stroke: "white", strokeWidth: 1.5}
        switch(route){
            case "bootstrap":
            case "worker":
            case "create":
                return <g>       
                    <circle style={circlestyle} cx="130" cy="200" r="5"></circle>
                    <circle style={circlestyle} cx="185" cy="200" r="5"></circle>
                    <circle style={circlestyle} cx="411" cy="196" r="5"></circle>
                    <circle style={circlestyle} cx="459" cy="196" r="5"></circle>
                </g>
        }
       
        
    }
    
    render(){
        
        const hexcontainerclass = this.props.active ? style.hexcontaineractive : style.hexcontainer;
        const hexclass = this.props.active ? style.hexactive : style.hex;
        const routecontainerclass = this.props.active ? style.routeactive : style.route;
        return (
            <g class={hexcontainerclass}>
                <path class={hexclass} d="M314.453,95.08l97.109,54.16l0,108.322l-97.109,54.16l-97.109,-54.16l0,-108.322l97.109,-54.16Z" style="stroke:#3a7bd4;stroke-width:4.5px;"/>  
                {this.props.active && this.renderRoute(this.props.route)}
                <path d="M216.604,164.428l30.468,17.322l0,34.642l-30.468,17.321l-30.468,-17.321l0,-34.642l30.468,-17.322Z" style="fill:#fff;stroke:#3a7bd4;stroke-width:4.5px;"/>
                
                <text x="200px" y="215.082px" style="font-family:'ArialRoundedMTBold', 'Arial Rounded MT Bold', sans-serif;font-size:40px;fill:#3a7bd4;">N</text>
               
                <path d="M217.694,256.805l0.452,-14.022l108.126,60.913l-12.398,8.234l-96.18,-55.125Z" style="fill:#3a7bd4;"/>
                <g transform="matrix(0.88613,0.463436,-0.463436,0.88613,-1604.7,1726.79)">
                    <text x="936.68px" y="-2149.55px" style="font-family:'GillSans', 'Gill Sans', sans-serif;font-size:8.69px;fill:#fff;">kubelet</text>
                </g>
                <g transform="matrix(0.88613,0.463436,-0.463436,0.88613,-1593.84,1715.17)">
                    <text x="965.761px" y="-2133.26px" style="font-family:'GillSans', 'Gill Sans', sans-serif;font-size:8.69px;fill:#fff;">kubelego</text>
                </g>
                <path d="M281.646,293.023l5.438,-9.052l37.315,21.193l-10.55,5.92l-32.203,-18.061Z" style="fill:#66676a;"/>
                <g transform="matrix(0.869676,0.493624,-0.493624,0.869676,-1784.74,1722.63)">
                    <text x="1098.21px" y="-2266.15px" style="font-family:'GillSans', 'Gill Sans', sans-serif;font-size:8.69px;fill:#fff;">docker</text>
                </g>

                <g class={routecontainerclass}>
                    <rect x="262.868" y="129.394" width="36.328" height="136.398" style="fill:#fff;stroke:#3a7bd4;stroke-width:0.8px;stroke-dasharray:0,0.04,4.8,4;"/>
                    <path style={rectstyle(this.props.route==="bootstrap" && this.props.active)} d="M294.464,134.468c0,-0.632 -0.513,-1.144 -1.144,-1.144l-23.76,0c-0.632,0 -1.145,0.512 -1.145,1.144l0,16.619c0,0.632 0.513,1.145 1.145,1.145l23.76,0c0.631,0 1.144,-0.513 1.144,-1.145l0,-16.619Z" />
                    <path style={rectstyle(this.props.route==="worker" && this.props.active)} d="M294.465,216.212c0,-0.632 -0.513,-1.145 -1.144,-1.145l-23.76,0c-0.632,0 -1.145,0.513 -1.145,1.145l0,16.619c0,0.631 0.513,1.144 1.145,1.144l23.76,0c0.631,0 1.144,-0.513 1.144,-1.144l0,-16.619Z" />
                    <path style={rectstyle(this.props.route==="worker" && this.props.active)} d="M294.69,243.46c0,-0.632 -0.513,-1.145 -1.145,-1.145l-23.76,0c-0.632,0 -1.144,0.513 -1.144,1.145l0,16.619c0,0.631 0.512,1.144 1.144,1.144l23.76,0c0.632,0 1.145,-0.513 1.145,-1.144l0,-16.619Z" />
                    <path style={rectstyle(this.props.route==="worker" && this.props.active)} d="M294.516,161.716c0,-0.632 -0.513,-1.145 -1.144,-1.145l-23.76,0c-0.632,0 -1.145,0.513 -1.145,1.145l0,16.619c0,0.632 0.513,1.145 1.145,1.145l23.76,0c0.631,0 1.144,-0.513 1.144,-1.145l0,-16.619Z" />
                    <path style={rectstyle(this.props.route==="create" && this.props.active)} d="M294.465,188.964c0,-0.632 -0.513,-1.145 -1.144,-1.145l-23.76,0c-0.632,0 -1.145,0.513 -1.145,1.145l0,16.619c0,0.632 0.513,1.145 1.145,1.145l23.76,0c0.631,0 1.144,-0.513 1.144,-1.145l0,-16.619Z"/>          
                    <text x="272.525px" y="144.225px" style={textstyle(this.props.route==="bootstrap" && this.props.active)}>bootstrap</text>
                    <text x="277.89px" y="226.057px" style={textstyle(this.props.route==="worker" && this.props.active)}>siteb</text>
                    <text x="278.461px" y="252.666px"style={textstyle(this.props.route==="worker" && this.props.active)}>sitec</text>
                    <text x="277.012px" y="171.193px" style={textstyle(this.props.route==="worker" && this.props.active)}>sitea</text>
                    <text x="275.592px" y="198.862px" style={textstyle(this.props.route==="create" && this.props.active)} >create</text>
                </g>

                 {this.props.active && this.renderJoins(this.props.route)}
            </g> 
        );    
    }
}


