                    <path d="M43.901,230.676c0,-1.762 -1.43,-3.192 -3.192,-3.192l-12.435,0c-1.761,0 -3.192,1.43 -3.192,3.192l0,28.131c0,1.762 1.431,3.192 3.192,3.192l12.435,0c1.762,0 3.192,-1.43 3.192,-3.192l0,-28.131Z" style="fill:#fff;stroke:#3a7bd4;stroke-width:1px;"/>     
